const initDare = () => {
    const calendarGrid = document.querySelector('.calendar-grid');
    const monthYear = document.getElementById('month-year');
    const prevBtn = document.getElementById('prev-month');
    const nextBtn = document.getElementById('next-month');
    const selectedDateDiv = document.getElementById('selected-date');

    let selectedDate = null;
    let currentDate = new Date();

    if (calendarGrid) {
        function renderCalendar(date) {
            calendarGrid.querySelectorAll('.day').forEach(day => day.remove());
        
            const year = date.getFullYear();
            const month = date.getMonth();
        
            // Отображение месяца и года
            monthYear.textContent = date.toLocaleString('ru', { month: 'long', year: 'numeric' });
        
            // Первый день месяца (номер дня недели 0-6, где 0 - воскресенье)
            const firstDay = new Date(year, month, 1);
            let startDay = firstDay.getDay(); // в JS воскресенье=0
            if (startDay === 0) startDay = 7; // сдвигаем, чтобы понедельник=1
        
            // Кол-во дней в месяце
            const daysInMonth = new Date(year, month + 1, 0).getDate();
        
            // Добавляем пустые ячейки перед первым днем месяца
            for (let i = 1; i < startDay; i++) {
                const emptyCell = document.createElement('div');
                emptyCell.classList.add('day', 'disabled');
                calendarGrid.appendChild(emptyCell);
            }
        
            // Добавляем дни месяца
            for (let dayNum = 1; dayNum <= daysInMonth; dayNum++) {
                const dayCell = document.createElement('div');
                dayCell.classList.add('day');
                dayCell.textContent = dayNum;
        
                const cellDate = new Date(year, month, dayNum);
        
                // Не даём выбрать даты в прошлом (пример)
                const now = new Date();
                now.setHours(0,0,0,0);
                if (cellDate < now) {
                dayCell.classList.add('disabled');
                } else {
                dayCell.addEventListener('click', () => {
                    if (selectedDate) {
                    selectedDate.classList.remove('selected');
                    }
                    dayCell.classList.add('selected');
                    selectedDate = dayCell;
        
                    selectedDateDiv.textContent = `Вы выбрали: ${cellDate.toLocaleDateString('ru')}`;
                });
                }
        
                    calendarGrid.appendChild(dayCell);
                }
                }
        
            if (prevBtn) {
                prevBtn.addEventListener('click', () => {
                currentDate.setMonth(currentDate.getMonth() - 1);
                renderCalendar(currentDate);
                });
            }
          
        
            if(nextBtn) {
                nextBtn.addEventListener('click', () => {
                currentDate.setMonth(currentDate.getMonth() + 1);
                renderCalendar(currentDate);
                });
            }
           
        
            // Инициализация
            renderCalendar(currentDate);
    }
   


}
export{initDare};